.activity-block-container {
  position: relative;
  margin: 1rem;
}

.accent-bar {
  position: absolute;
  inset: 0 0 65%;
  border-radius: 1.5rem;
}

.activity-data-display-container {
  background-color: var(--tile-background);
  position: absolute;
  inset: 22% 0 0 0;
  border-radius: 1.5rem;
}